import { FormHelperText } from "@mui/material";
import { DatePicker } from "@mui/x-date-pickers";
import { useEffect, useState } from "react";
import { Control, Controller } from "react-hook-form";

type CustomDatePickerFieldProps = {
	control: Control<any, any>;
	name: string;
	label: string;
	errors: any;
	onInputChange?: (value: Date) => void;
	[other: string]: any;
};

const CustomDatePickerField = (props: CustomDatePickerFieldProps) => {
	const { control, name, label, errors, onInputChange, ...other } = props;
	const [customClass, setCustomClass] = useState("mt-12 shrink-0 ");

	useEffect(() => {
		if (other["dialog"]) {
			setCustomClass((state) => state + "w-full ");
		} else if (other["double"]) {
			setCustomClass((state) => state + "w-[49%] ");
		} else if (other["filter"]) {
			setCustomClass((state) => state + "flex-1 ");
		} else {
			setCustomClass((state) => state + "md:mx-6 w-full md:w-[45%] ");
		}
		return () => {
			setCustomClass("mt-12 shrink-0 ");
		};
	}, []);

	return (
		<Controller
			name={name}
			control={control}
			render={({ field }) => (
				<div className={`flex flex-col flex-1 ${other["dialog"] ? "w-full" : other["double"] ? "w-[49%]" : ""}`}>
					<DatePicker
						className={customClass}
						{...field}
						label={label}
						{...other}
						{...(onInputChange ? { onChange: (value: Date) => onInputChange(value) } : {})}
					/>
					<FormHelperText error>{errors[name]?.message || " "}</FormHelperText>
				</div>
			)}
		/>
	);
};

export default CustomDatePickerField;
